body {
  background-color: #CDD1C4;
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
}

.header-block {
  background-color: #30323D;
  padding: 70px;
  text-align: center;
  font-size: 44px;
  font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
  font-stretch: expanded;
  color: #5C80BC;
}

.images {
  width: 600px;
  margin: auto;
  display: block;
}

.button {
  background-color: #4CAF50; /* Green */
  border: none;
  color: white;
  padding: 15px 32px;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 16px;
}

.block-paragraph {
  text-indent: 50px;
}

.content {
  width: 60%;
  margin: 15px;
  margin-bottom: 200px;
  padding: 25px;
  border: 5px solid #303030;
  border-radius: 20px;
  float: left;
  /* background-color: #30323D; */
}

.navBar {
  background-color: #4D5061;
  border-width: 5px;
  /* border-color: #5C80BC; */
  /* border-style: solid; */
  padding: 20px;
  text-align: center;
  font-size: 25px;
  float: left;
  margin: 15px;
  border-radius: 20px;
}

.footer {
  position: fixed;
  left: 0;
  right: 0;
  background-color: rgba(77, 80, 97, 1);
}

.link {
  color: #E8C547;
}

